var DB = new VueDB();

export default{
data(){
return {
goodsSellOut:"../../../static/images/home/sellOut.jpg",
frClass:null,
banner: [{//轮播图
figureAddress:"../../../static/images/classStore/swiper1.jpg",
}
],
swbtrsp:[{ //天然食品
trsp:"../../../static/images/classStore/swiperBBT.jpg",
},{
trsp:"../../../static/images/classStore/swiperBBR.jpg",
},{
trsp:"../../../static/images/classStore/swiperBBS.jpg",
},{
trsp:"../../../static/images/classStore/swiperBBP.jpg",
}],
hotSell:"../../../static/images/classStore/hotSell.jpg",
classData11:[{ //热门分类
classLogo:"../../../static/images/classStore/hsApple.jpg",
cssParam:"apple",
},{
classLogo:"../../../static/images/classStore/hsPeash.jpg",
cssParam:"peach",
},{
classLogo:"../../../static/images/classStore/hsPepper.jpg",
cssParam:"pepper",
},{
classLogo:"../../../static/images/classStore/hsOther.jpg",
cssParam:"other",
}],
classData:[], //所有分类数据
classData0:[],//分类1的数据
classData1:[],//分类2的数据
classData2:[],//分类3的数据
classData3:[],//分类4的数据
classData22:[{ //店铺分类
cssName:"../../../static/images/classStore/classApple1.png",
cssParam:"apple",
cssItem:["../../../static/images/classStore/csHgs.jpg",
"../../../static/images/classStore/csFjc.jpg",
"../../../static/images/classStore/csGlt.jpg",
"../../../static/images/classStore/csVacuo.jpg",
],
},{
cssName:"../../../static/images/classStore/classPeach1.png",
cssParam:"peach",
cssItem:["../../../static/images/classStore/csHgs.jpg",
"../../../static/images/classStore/csFjc.jpg",
"../../../static/images/classStore/csGlt.jpg",
"../../../static/images/classStore/csVacuo.jpg",
],
},{
cssName:"../../../static/images/classStore/classPepper1.png",
cssParam:"pepper",
cssItem:["../../../static/images/classStore/csHgs.jpg",
"../../../static/images/classStore/csFjc.jpg",
"../../../static/images/classStore/csGlt.jpg",
"../../../static/images/classStore/csVacuo.jpg",
],
},{
cssName:"../../../static/images/classStore/classOther1.png",
cssParam:"other",
cssItem:["../../../static/images/classStore/csHgs.jpg",
"../../../static/images/classStore/csFjc.jpg",
"../../../static/images/classStore/csGlt.jpg",
"../../../static/images/classStore/csVacuo.jpg",
],
}],

}
},
mounted(){



this.initData(this.frClass)
},
methods:{
initData(){
var _this = this;
console.log(this.gwcRedPoint)
this.$http.get("/userLogin/queryClassStore").then((res) => {
console.log("queryClassStore")
console.log(res.data.data.data)
var resData = res.data.data.data;
this.classData = resData;

this.changeClassData(resData); 
}).catch((err) => {
console.log(err)
})
},

changeClassData(item){
console.log(item[0])
item[0].storeClassLogo = "../../../static/images/classStore/classApple1.png";
this.classData0 = item[0];

item[1].storeClassLogo = "../../../static/images/classStore/classPeach1.png";
this.classData1 = item[1];
item[2].storeClassLogo = "../../../static/images/classStore/classPepper1.png";
this.classData2 = item[2];
item[3].storeClassLogo = "../../../static/images/classStore/classOther1.png";
this.classData3 = item[3];
},
onItemClick (index) {
console.log('on item click:', index)
},
goStoreList(link,param){
console.log("222222")
this.$router.openPage(link,{params:param});
},
goClassification(link,param){
DB.setItem("qaStoreNo",{qaStoreNo:param});
this.$router.openPage(link,{params:param});
}
},
components: {
Swiper,
SwiperItem,
headerBack,
XImg,
Divider
},


}



